  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'ProximaNova_Bd';
  src: url('../fonts/ProximaNova-Bold.eot');
  src: url('../fonts/ProximaNova-Bold.eot?#iefix') format('embedded-opentype'), url('../fonts/ProximaNova-Bold.woff2') format('woff2'), url('../fonts/ProximaNova-Bold.woff') format('woff'), url('../fonts/ProximaNova-Bold.ttf') format('truetype'), url('../fonts/ProximaNova-Bold.svg#ProximaNova-Bold') format('svg');
  font-weight: bold;
  font-style: normal; }

@mixin reg() {
	font-family: 'ProximaNova_Rg', sans-serif; }
@mixin bold() {
	font-family: 'ProximaNova_Bd', sans-serif; }

@import "colors";
@import "smartGrid";


@include reset();
.debug {
	@include debug($dbbgcol, $dboutlcol); }

iframe {
	width: 100%;
	height: 400px;
	border: none; }


html {
	font-size: 10px; }
body {
	@include reg(); }
section {
	padding: 10rem 0;
	@include md(padding-top, 6rem);
	@include sm(padding-top, 4rem); }
.btn {
	font-size: 1.2rem;
	background-color: $btnColor;
	padding: 1rem 3rem;
	border: 1px solid darken($btnColor, 5%);
	border-radius: 0.8rem;
	color: $white;
	text-transform: uppercase;
	transition: all 0.5s;
	&:hover {
		cursor: pointer;
		background-color: darken($btnColor, 5%); } }
.wrapper {
	@include wrapper(); }

.form {
	font-size: 1.6rem;
	width: 85%;
	margin: 0 auto;
	text-align: center;
	&-inp, &-textarea {
		box-sizing: border-box;
		margin: 0 0;
		border: 1px solid $borderInp;
		border-radius: 0.8rem;
		width: 100%;
		padding: 1.7rem; }
	&-lbl {
		font-size: 1.3rem;
		display: inline-block;
		position: relative;
		top: 1.1rem;
		padding: 0.5rem;
		background-color: $white; }
	&-btn {
		box-sizing: border-box;
		font-size: 1.6rem;
		width: 100%;
		padding: 1.7rem;
		margin: 2.5rem 0 1.5rem; } }



header {
	background: #fff url(../images/main-bg.jpg) center center no-repeat;
	background-size: cover;
	@include md(background-position, top right);
	@include sm(background-position, top left);
	section:first-child {
		padding: 3rem 0; } }
.top {
	&-row {
		@include row-flex();
		justify-content: space-between; }
	&-item {
		@include col();
		display: flex;
		align-items: center; }
	&-logo {
