			padding-top: 1.5rem;
			padding-bottom: 1.5rem; } } }

.introduce {
	.int-row {
		@include row-flex();
		justify-content: space-between;
		@include sm(justify-content, center);
		.int-item {
			@include col();
			display: flex;
			flex-direction: column; }
		.int-info {
			@include size (7);
