			@include sm(align-items, center);
			h1 {
				@include bold();
				font-size: 4rem;
				text-transform: uppercase;
				line-height: 1.45;
				@include sm(text-align, center);
				@media screen and (max-width: 512px) {
					font-size: 3rem; }
				@media screen and (max-width: 380px) {
					font-size: 2.5rem; } }
			p {
				display: block;
				font-size: 2.4rem;
				margin-top: 4rem; }
			.int-banner {
				font-size: 1.8rem;
				line-height: 3rem;
				text-align: center;
				padding: 3rem 5rem;
				background-color: $iconsColor;
				color: $white;
				strong {
					@include bold();
					font-size: 3rem; } }
			.int-trigger {
				@include bold();
				text-transform: uppercase;
				font-size: 2.2rem;
				margin: 3rem 0 2rem 0; } }
		.int-form {
			@include size(4);
			@include size-md(5);
			@include size-sm(8);
			@include size-xs(12);
			align-items: flex-end;
			@media screen and (max-width: 780px) {
				align-items: center;
				align-self:: center {}
				margin-top: 3rem; }
			&-container {
				display: flex;
				flex-direction: column;
				background-color: $white;
				opacity: 0.8;
				width: 90%;
				text-align: center;
				padding-bottom: 3rem; }
			&-header {
				font-size: 1.8rem;
				margin: 5rem 4rem 1rem 4rem;
