.slick-active {
	outline: none; }
.arrow, .feedback-slider-btn {
	top: 50%;
	transform: translateY(-50%);
	position: absolute;
	z-index: 99;
	width: 2rem;
	height: 2rem;
	border: none;
	cursor: pointer; }
.prev {
	left: 0;
	background: url(../images/arrow-left.svg)	center no-repeat;
	background-size: 70%; }
.next {
	right: 0;
	background: url(../images/arrow-right.svg)	center no-repeat;
	background-size: 70%; }
.feedback-prev-btn {
	left: 0;
	background: url(../images/feedBack/prev.png)	center no-repeat;
	background-size: 50%; }
.feedback-next-btn {
	right: 0;
	background: url(../images/feedBack/next.png)	center no-repeat;
	background-size: 50%; }


.prices {
	background: $iconsColor url(../images/prices-bg.jpg) center;
