.feedback {
	background: url(../images/feedback-bg.jpg) no-repeat center;
	background-size: cover;
	position: relative;
	@media screen and (max-width: 960px) {
		.fotorama {
			margin-bottom: 3rem; } }
	&-prev-btn {
		left: -4rem;
		@media screen and(max-width: 780px) {
