					@include size-sm(10);
					@include size-xs(12);
					p {
						font-size: 1.6rem;
						margin-bottom: 1rem;
						strong {
							@include bold(); } }
					.media {
						display: flex;
						align-items: flex-start;
						padding: 1rem	1rem 1rem 0;
						margin: 3rem 0 1rem 0;
						font-size: 1.4rem;
						img {
							width: 60px;
							margin: 0 1rem 0 0;
							border-radius: 50%; }
						&-body {
							flex-grow: 1;
							padding-top: 0.5rem;
							h3 {
								@include bold(); } } } } } } }
	&-text {
		font-style: italic;
		padding: 1rem;
		border: 2px solid $iconsColor;
		position: relative;
		&::before {
			content: '';
			display: block;
			position: absolute;
			top: -2.2rem;
			left: 5rem;
			width: 2rem;
			height: 2rem;
			background-color: $white;
			border: 2px solid $iconsColor;
			border-bottom: none;
			border-top-right-radius: 100px; } }
	.fb-form-row {
		@include row-flex();
		justify-content: center;
		.row-item {
			@include col();
