				border: 1px solid green;
				h3 {
					@include bold();
					font-size: 2rem;
					margin-top: 4rem; }
				.fbf-row {
					display: flex;
					flex-wrap: wrap;
					justify-content: space-between;
					/*margin-top: 2rem*/
					.fb-form-part {
						display: border-box;
						width: 47%;
						@media screen and (max-width: 550px) {
							width: 100%; } } }
				button {
					width: 47%;
					margin-bottom: 4rem;
					@media screen and (max-width: 550px) {
