			margin-top: 3rem; }
		@media screen and (max-width: 400px) {
			@include size(12); }
		&-container {
			background-color: $white;
			opacity: 0.8;
			width: 90%;
			text-align: center;
			padding: 2rem 0;
			margin-top: 4rem;
			margin-left: 10%;
			@include sm(margin, auto);
			@media screen and (max-width: 1110px) {
				margin-left: 0;
				width: 100%; } }
		&-header {
			font-size: 1.8rem;
			margin: 2.5rem 0 1.5rem; }
		small {
			font-size: 1.3rem; } } }

footer {
	padding: 4rem 0; }
.footer {
	&-row {
		@include row-flex();
		justify-content: space-between;
		@include sm(justify-content, center); }
	&-item {
		@include col();
		@include size(3);
		@include size-sm(4);
		display: flex;
		flex-direction: column;
		align-content: center; }
	&-logo {
		@include size-xs(6);
		align-items: flex-start; }
	&-contacts {
		@include size-xs(10);
		align-items: center;
		justify-content: space-around;
		@media screen and (max-width: 560px) {
			order: -1;
			margin-bottom: 2rem;
			p {
				margin: 0.5rem 0; } } }

	&-developer {
		@include size-xs(6);
		align-items: flex-end; }
	&-link {
		text-decoration: none;
		display: inline-block;
		color: $text;
		border-bottom: 1px dotted $text;
		&:hover {
			color: darken($text, 10%);
			cursor: pointer; } }
	&-phone {
		@include bold();
		font-size: 1.5rem;
		text-decoration: none;
		color: $text; } }
















.popup {
	display: none;
	position: fixed;
	left: 0;
	top: 0;
	bottom: 0;
	right: 0;
	width: 100vw;
	height: 100vh;
	z-index: 9;
	background-color: rgba(0, 0, 0, 0.5);
	&-content {
		position: fixed;
		top: 10%;
		left: 50%;
		transform: translateX(-50%);
		padding: 2rem 4rem;
		background-color: $white;
		text-align: center; }
	&-close {
		position: absolute;
		top: -1.5rem;
		right: -2rem;
		width: 2rem;
		font-size: 3rem;
		text-align: center;
		color: $white;
		border: none;
		background-color: transparent; }
	&-header {
		font-size: 1rem;
		@include bold(); } }
.showPopap {
	display: block; }



.popup-form {
	margin-top: 3rem;
	text-align: center;
	input, textarea {
		box-sizing: border-box;
		display: block;
		width: 100%;
		border: 1px solid $borderInp;
		padding: 1.7rem;
		margin: 2rem 0;
		border-radius: 5px; }
	label {
		font-size: 1.3rem;
		box-sizing: border-box;
		position: absolute;
		transform: translateX(-50%) translateY(-50%);
		padding: 0.5rem;
		background-color: $white; }
	button {
		display: block;
		width: 100%;
		height: 5.5rem;
		font-size: 1.6rem; } }
.popup-close:hover {
	color: darken($white, 15%);
	cursor: pointer; }



